.wrapper {
  display: flex;
  height: 100vh;
  display: flex;
  padding: 1.5rem;
  gap: 1.5rem;
  & > div {
    flex: 1;
  }
}

.card {
  height: 100%;
  min-width: 52%;
  border-radius: 0.75rem;
  background: var(--base-gray-800, #16161f);
  display: flex;
  flex-direction: column;
  padding: 1rem;
  gap: 1rem;
}

.header {
  display: flex;
  gap: 1rem;
}

.logo {
  width: fit-content;
  padding: 0.5rem;
  border-radius: 0.5rem;
  background: var(--base-gray-600, #1e1e29);
  &:hover {
    cursor: pointer;
  }
  img {
    height: 2.5rem;
  }
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  border-radius: 0.5rem;
  background-size: cover;
}
.infoContainer {
  display: flex;
  justify-content: space-between;
  margin: 2rem;
  gap: 0.5rem;
  .place {
    font-size: 1.25rem;
    font-weight: 700;
  }
  .date {
    font-size: 1rem;
    font-weight: 400;
  }
}

.time {
  font-size: 1.25rem;
  font-weight: 700;
}

.addData {
  display: flex;
  justify-content: space-between;
}

.temperature {
  font-size: 6rem;
  font-weight: 800;
}

.weather {
  margin: 2rem 0 2rem 2rem;
}

.desc {
  font-size: 1.25rem;
}

.weatherLogo {
  width: 15.5rem;
  height: 15.5rem;
}

.rightSide {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  width: 40%;
  height: 100%;
}
.weatherDetails,
.details {
  border-radius: 0.75rem;
  background: var(--base-gray-800, #16161f);
  padding: 1.75rem 1.5rem 0.5rem 1.5rem;
}
.weatherDetails {
  height: 60%;
}
.details {
  height: 100%;
}
.detaildsTitle {
  color: var(--base-gray-400, #7f7f98);
  font-size: 1rem;
  font-weight: 400;
}
.detailsList {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 1rem;
  li {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--base-gray-700, #1c1c27);
    padding: 1rem;
    &:last-child {
      border-bottom: none;
    }
    div {
      display: flex;
      align-items: center;
      img {
        margin-right: 0.5rem;
      }
    }
  }
}
.forecastTitle {
  margin-bottom: 2rem;
}
.forecastList {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.forecastDay {
  flex: 1;
  max-width: 30%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.dayTitle {
  font-size: 0.875rem;
  font-weight: 700;
}

.dayIcon {
  width: 5rem;
  margin: auto;
}

.dayDesc {
  color: var(--base-gray-200, #bfbfd4);
  font-size: 0.875rem;
  font-weight: 400;
  margin-bottom: 1rem;
}

.maxTemp {
  color: var(--base-gray-100, #fafafa);
  font-size: 0.875rem;
  font-weight: 700;

  margin-right: 1rem;
}

.minTemp {
  color: var(--base-gray-400, #7f7f98);
  font-size: 0.875rem;
  font-weight: 700;
}

@media (max-width: 1024px) {
  .wrapper {
    flex-direction: column;
  }
  .rightSide {
    width: 100%;
    flex-wrap: wrap;
    flex-direction: row;
    & > div {
      height: fit-content;
      flex: 1;
      padding: 2rem 1rem;
    }
  }

  .forecastList {
    justify-content: space-between;
  }
}

@media (max-width: 768px) {
  .addData {
    flex-direction: column;
    align-items: center;
  }
  .rightSide {
    & > div {
      min-width: 100%;
    }
  }
  .forecastList {
    justify-content: space-around;
  }
}

@media (max-width: 420px) {
  .wrapper {
    padding: 0;
  }

  .forecastList {
    flex-direction: column;
  }
}
