.searchContainer {
  width: 100%;
  position: relative;
}

.input {
  background: #1e1e29;
  border-radius: 0.5rem;
  width: 100%;
  padding: 1rem;
  z-index: 2;
  &:focus {
    outline: none;
    & ~ .tooltip {
      visibility: visible;
      opacity: 1;
      left: 1rem;
    }
  }
  &::placeholder {
    transition: color 0.2s;
    color: var(--base-gray-400, #7f7f98);
  }
}
.predictionListContainer {
  position: absolute;
  width: 100%;
  display: flex;
  flex-direction: column;
  z-index: 2;
  top: 3rem;
  background-color: #3b3b54;
  border-radius: 0.5rem;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.2s, top 0.2s;
}
.input:focus ~ .predictionListContainer {
  opacity: 1;
  top: 4.5rem;
}
.predictionContainer {
  border-bottom: 1px solid var(--base-gray-600, #1e1e29);
  &:hover {
    cursor: pointer;
  }
  p {
    margin: 1rem;
    width: 100%;
  }
}
.tooltip {
  position: absolute;
  color: #7f7f98ac;
  font-size: 0.7rem;
  top: 3rem;
  left: 0.6rem;
  opacity: 0;
  visibility: none;
  transition: opacity 0.2s, left 0.2s;
  button {
    background-color: transparent;
    text-decoration: underline;
    &:hover {
      cursor: pointer;
    }
  }
}

.hidden {
  display: none;
}
