.wrapper {
  position: relative;
  width: 100vw;
  height: 100vh;
  padding: 2rem;
  background-image: url("../../assets/bg.png");
}

.container {
  height: 52vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
// logo
.logoContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}
.logoIcon {
  height: 4rem;
  width: 4rem;
}

.logoText {
  font-size: 1.5rem;
}
// content
.searchText {
  margin-bottom: 3.5rem;
}
.title {
  color: var(--base-gray-100, #fafafa);
  font-size: 2rem;
  font-weight: 700;
  a {
    color: var(--product-blue-light, #8fb2f5);
    &:hover {
      text-decoration: underline;
    }
  }
}
.desc {
  color: var(--base-gray-200, #bfbfd4);
}

.keys {
  width: fit-content;
  display: flex;
  justify-content: space-between;
  gap: 5rem;
  margin: 3rem auto;
}

.googleKeyContainer,
.weatherKeyContainer {
  position: relative;
}
.keyInput {
  background: var(--base-gray-600, #1e1e29);
  border-radius: 0.5rem;
  padding: 1rem;
  &:focus {
    outline: none;
    &::placeholder {
      color: transparent;
    }
  }
  &::placeholder {
    transition: color 0.2s;
    color: var(--base-gray-400, #7f7f98);
  }
}

.weatherKey,
.googleKey {
  &:focus {
    & ~ .tooltip {
      opacity: 1;
      left: 1rem;
    }
  }
}
.tooltip {
  position: absolute;
  color: #7f7f98ac;
  font-size: 0.7rem;
  top: 3rem;
  left: 0.6rem;
  opacity: 0;
  transition: opacity 0.2s, left 0.2s;
}

@media (max-width: 768px) {
  .keys {
    flex-direction: column;
  }
}
