@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;700;800&family=Roboto+Condensed:wght@300;400;500;700&family=Roboto:wght@400;500;700&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
  color: #fff;
  background: var(--base-gray-900, #13131a);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  outline: none;
}

* {
  box-sizing: border-box;
  border: none;
  color: inherit;
  margin: 0;
  padding: 0;
}

/*  */

.App {
  max-height: 100vh;
  position: relative;
}

.content {
  width: 100%;
  position: absolute;

  opacity: 1;
  transition: opacity 0.3s, top 2s;
}
.loaderContainer {
  position: absolute;
  width: calc(100% - 2rem);
  opacity: 1;
  transition: opacity 1.2s;
}
.show {
  opacity: 1;
}

.hide {
  opacity: 0;
}
